.divheader{background-color:#f4f4f4;border-bottom:1px solid rgba(0,0,0,0.12)}.topBar{flex-wrap:wrap;font-family:Roboto;font-size:16px;position:relative;z-index:2;justify-content:space-between;display:flex;box-sizing:border-box;padding:0 16px;width:100%;flex-direction:row;align-items:center;white-space:nowrap;margin-left:0px;width:calc(100% - 57px);transition:margin 0ms,width 0ms}.topBar.with-custom-styles{background-color:#767676}@media screen and (min-width: 801px){.topBar.with-side-bar{margin-left:240px;width:calc(100% - 299px)}}@media (max-width: 1187px) and (min-width: 801px){.topBar.with-side-bar{margin-left:240px;width:calc(100% - 298px)}}@media (max-width: 800px) and (min-width: 500px){.topBar.with-side-bar{margin-left:240px;width:calc(100% - 293px)}}
