body{width:100%;height:100%;overflow-y:hidden;overflow-x:hidden;position:absolute !important}.connector{background-color:#ffffff;width:100%;height:100%;margin-left:-1px;text-align:center}.panelconnectortitle{text-align:center;padding-top:41px}.app{display:flex;height:100vh}.app .spinner{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(51,51,51,0.5);z-index:500}.app .spinner .spinnerPath{stroke:#333}.app .productpanel{background-color:#f4f4f4;border-left:1px solid rgba(0,0,0,0.12);-webkit-box-sizing:border-box;box-sizing:border-box;display:-webkit-box;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:relative;width:62px;text-align:center;margin-top:-66px;padding-right:5px;padding-top:10px}.app .btnselect{background-color:#001978;-webkit-border-radius:3px 0 0 3px;border-radius:3px 0 0 3px;display:none;height:40px;position:absolute;right:1px;top:4px;width:3px}.app .productsbutton{padding-bottom:15px}.app spaceproduct{border-top:1px solid #dadce0;content:'';display:block;-webkit-box-flex:1 0 auto;-webkit-flex:1 0 auto;flex:1 0 auto;margin:0 auto;padding-bottom:16px;width:20px}.app .content-wrapper{flex-grow:1;display:flex;margin-left:0px;width:100%;transition:margin 0ms}.app .content-wrapper.with-side-bar{margin-left:240px;width:calc(100% - 299px)}.app .content-wrapper .message-grid{background-color:#ffffff;flex-grow:1;min-width:0}.app .content-wrapper .message-viewer,.app .content-wrapper .message-editor{background-color:#ffffff;flex-grow:1;min-width:0;margin-bottom:130px}.app .content-wrapper .fab-container{position:fixed;right:16px;bottom:16px;width:60px;display:flex;flex-direction:row;justify-content:space-between;z-index:9999}
