.messageList{position:relative;display:flex;flex-direction:column;justify-content:center}.messageList .clearFolderListItem{display:flex}.messageList .clearFolderListItem .emptyFolder{margin:0 auto;font-size:.8rem}.messageList .list{flex:1;padding:0;height:100%}.messageList .list .virtualList{outline:none}.messageList .list .virtualList .item{display:flex;font-weight:700;font-size:.8rem;padding:0px;height:43px;border:1px solid #f7f7f7;cursor:pointer}.messageList .list .virtualList .item.seen{font-weight:400}.messageList .list .virtualList .item.deleted{color:#a6a6a6;font-style:italic}.messageList .list .virtualList .item .itemDetails{flex-grow:1;min-width:0;width:1px;display:flex;flex-flow:row nowrap}.messageList .list .virtualList .item .itemDetails .from{width:200px;min-width:200px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.messageList .list .virtualList .item .itemDetails .flag{display:block;width:20px;min-width:20px;overflow:hidden;visibility:hidden;font-size:18px;line-height:20px}.messageList .list .virtualList .item .itemDetails .flag.flagged{visibility:visible;color:#767676}.messageList .list .virtualList .item .itemDetails .subject{flex-grow:1;margin-left:4px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.messageList .list .virtualList .item .itemDetails .receivedDate{width:90px;min-width:90px;text-align:right}.messageList .list .virtualList .item .itemDetails .size{margin-right:8px;width:85px;min-width:85px;text-align:right}.messageList .list .virtualList .item .itemDetails:hover .flag{visibility:visible}.messageList .listSpinner{position:absolute;bottom:0px;z-index:2;height:25px;width:100%;overflow:hidden;pointer-events:none}.messageList .listSpinner .listSpinnerCanvas{width:16px}
