.listItem{cursor:pointer;-moz-user-select:none;-ms-user-select:none;user-select:none;display:flex}.listItem .graphic{margin-right:16px}.listItem .primaryText{margin:0px;width:1px;min-width:1px;flex:1}.listItem .primaryText.hasNewMessages{font-weight:700}.listItem .actions{display:none;margin-right:-6px}.listItem .actions .contextMenu{display:none;transform:translateX(100%)}.listItem .actions .contextMenu.visible{display:block;transform:translateX(0%);animation:slide-in 0.5s forwards}@keyframes slide-in{0%{transform:translateX(100%)}100%{transform:translateX(0%)}}.listItem:hover .actions{display:block}
