.attachment{position:relative;display:flex;align-items:center;background-color:#f7f7f7;margin:0 6px 6px 0;padding:6px;max-width:200px;cursor:pointer}.attachment:hover{background-color:#e6e6e6}.attachment.downloading{cursor:initial}.attachment .spinner{position:absolute;top:0px;left:0px;height:100%;width:100%;background-color:rgba(204,204,204,0.6)}.attachment .spinner .canvas{height:22px}.attachment .icon{margin-right:6px}.attachment .fileInfo{overflow:hidden}.attachment .fileInfo .fileName{display:block;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;font-size:0.9rem;color:#404040}.attachment .fileInfo .size{display:block;font-size:0.7rem;color:#595959}
