.messageViewer{position:relative;padding:16px;display:flex;flex-direction:column}.messageViewer .header{width:100%}.messageViewer .header h1.subject{margin-top:0;font-size:1.25rem;font-weight:500}.messageViewer .header h1.subject .folder{margin-left:16px;height:15px;line-height:15px;font-size:11px}.messageViewer .header .fromDate{display:flex;flex-flow:row nowrap;min-width:0}.messageViewer .header .fromDate .from{display:inline-block;flex-grow:1;min-width:0;width:1px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.messageViewer .header .fromDate .from .fromName{font-size:1.15rem;margin-right:8px}.messageViewer .header .fromDate .from .email{color:#a6a6a6;font-style:italic}.messageViewer .header .fromDate .date{display:inline-block;white-space:nowrap}.messageViewer .header .to{color:#a6a6a6}.messageViewer .header .to .name{margin-right:8px;color:#737373}.messageViewer .body{flex:1;height:0px;margin-top:16px;max-width:100%;width:100%;display:inline-block;position:relative;overflow:auto}.messageViewer .body .attachments{display:flex;flex-direction:row;flex-wrap:wrap;margin-bottom:16px}.messageViewer .body img{max-width:100%}.messageViewer .body p{margin:0 auto}
