.login--background{background:url("https://source.unsplash.com/collection/4509660/1920x1080") no-repeat #fff;background-size:cover}@media screen and (max-width: 1280px){.login--background{background-image:url("https://source.unsplash.com/collection/4509660/1280x720")}}@media screen and (max-width: 480px){.login--background{background:none}}.login--background .login--container{background-color:rgba(0,0,0,0.5);width:100%;min-height:100vh;height:100%;display:flex;align-items:center;justify-content:center}.login--background .login--container .spinner{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(51,51,51,0.5);z-index:500}.login--background .login--container .spinner .spinnerPath{stroke:#333}.login--background .login--container .card{padding:16px;width:60%;max-width:800px;min-width:480px}@media screen and (max-width: 480px){.login--background .login--container .card{width:100%;height:100vh;min-width:auto;border-radius:0;overflow-y:scroll}}.login--background .login--container .card .title{font-size:1.8rem;text-align:center}.login--background .login--container .card .subtitle{margin-top:8px;font-size:1.5rem;color:#737373;text-align:center}.login--background .login--container .card .section{margin:16px 0;font-size:1.2rem;color:#737373}.login--background .login--container .card .advancedButton{padding:0 4px 0 0}.login--background .login--container .card .advancedContainer{margin-top:16px}.login--background .login--container .card .fullWidth{width:100%}.login--background .login--container .card .formField{margin-bottom:16px}.login--background .login--container .card .server{width:100%;display:flex}.login--background .login--container .card .server .serverHost{margin-right:16px;flex-grow:1}.login--background .login--container .card .server .serverPort{width:180px}.login--background .login--container .card .loginButton{margin-top:16px}
